import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { TransitionGroup, Transition } from 'react-transition-group';
import renderHTML from 'react-render-html';
import './main.css';

var axios = require('axios');

//------------------- FadeTransition ------------------------------

const duration = 200;

const defaultStyle = {
	transition: `transform ${duration}ms ease-in-out`,
	transform: 'translateX(-120%)'
};

const transitionStyles = {
	entering: { transform: 'translateX(120%)' },
	entered:  { transform: 'translateX(0)' }
};

const FadeTransition = (props) => (
	<Transition
		in={true}
		{...props}
		timeout={duration}
	>
		{(state) => {
			return (
				<div style={{
					...defaultStyle,
					...transitionStyles[state]
				}}>
					{props.children}
				</div>
			);
		}}
	</Transition>
);
FadeTransition.propTypes = {
};

//------------------- Loading ------------------------------

function Loading(props) {
	return (
		<FadeTransition {...props}>
			<p>Henter data...</p>
		</FadeTransition>
	);
}
Loading.propTypes = {
};

//------------------- TestIntro ------------------------------

function TestIntro(props) {
	return (
		<FadeTransition {...props}>
		<div className="test-wrapper--intro">
			{renderHTML(props.intro)}
			<button onClick={props.start}>Start testen</button>
		</div>
		</FadeTransition>
	);
}
TestIntro.propTypes = {
	intro: PropTypes.string.isRequired,
	start: PropTypes.func.isRequired
};

//------------------- TestStep ------------------------------

function TestStep(props) {
	var info = props.info;
	var onClick = props.onClick;
	var answered = props.answered;

	var answers_for_this_question = info.answers.map((a) => a.answer_id);

	return (
		<div className="test-wrapper--step">
			<h2>{info.question}</h2>
			{info.text && info.text.map(
				(p,i) => {
					return (
						<p key={i}>{p}</p>
					);
				}
			)}
			{info.answers && info.answers.map(
				(answer) => {
					var className;
					if (answered.includes(answer.answer_id)) className = "active";
					return (
						<div key={answer.answer_id}>
						  <button className={className} onClick={onClick.bind(null,answer.answer_id, answers_for_this_question)}>{answer.text}</button>
						</div>
					);
				}
			)}
			{info.extra && renderHTML(info.extra)}
		</div>
	);
}
TestStep.propTypes = {
	info: PropTypes.object.isRequired,
	onClick: PropTypes.func.isRequired,
	answered: PropTypes.array.isRequired
};

//------------------- Question ------------------------------

function Question(props) {
	var question      = props.question;
	var given_answers = props.given_answers;
	var recordAnswerAndStep = props.recordAnswerAndStep;
	let onClick       = (add_answer, del_answers) => {
		recordAnswerAndStep(add_answer, del_answers);
	};

	return (
        <FadeTransition {...props}>
		<div className="Question">
		  <TestStep info={question} onClick={onClick} answered={given_answers}/>
		</div>
		</FadeTransition>
	);
}

Question.propTypes = {
	question: PropTypes.object.isRequired,
	given_answers: PropTypes.array.isRequired,
	recordAnswerAndStep: PropTypes.func.isRequired
};

//------------------- Result ------------------------------

function Result(props) {
	return (
        <FadeTransition {...props}>
		<div className="test-wrapper--result">
			{renderHTML(props.html)}
		</div>
		</FadeTransition>
	);
}
Result.propTypes = {
	html:  PropTypes.string.isRequired
};

//------------------- Status ------------------------------

function Status(props) {
	var steps = [];
	for (let i = 0; i < props.count; ++i) {
		steps[i] = "";
	}
	for (let i = 0; i < props.step-1; ++i) {
		steps[i] += 'seen ';
	}
	steps[props.step  - 1] += 'active ';
	steps[props.count - 1] += 'final ';
	return (
		<ul className="test-wrapper--status">
			{steps.map(function(classes,i) {
				return (<li key={i} className={classes}><i></i></li>);
			})}
		</ul>
	);
}
Status.propTypes = {
	step:  PropTypes.number.isRequired,
	count: PropTypes.number.isRequired
};

//------------------- DanmarkTesten ------------------------------

class DanmarkTesten extends Component {
	constructor(props) {
		super(props);

		if (props.json) { // test inject
			this.state = {
				given_answers: [],
				step:    0,
				steps:   props.json.questions[props.json.questions.length - 1].step+1,
				count:   props.json.questions.length + 1,
				json:    props.json
			};
		}
		else {
			this.state = {
				given_answers: [],
				step:    0
			};

			var prefix = process.env.PUBLIC_URL || "http://localhost:3000";

			axios.get(prefix + "/" + props.quiz + ".json").then((res) => {
				var json = res.data;
				this.setState(() => {
					return {
						steps: json.questions[json.questions.length - 1].step+1,
						count: json.questions.length + 1,
						json:  json
					};
				});
			}).catch((e) => {
				if (console.warn) console.warn(e);
			});
		}
	}

	startTest = () => {
		this.setState(() => {
			return { step: 1 };
		});
	};

	recordAnswerAndStep = (answer, del_answers) => {

		let given_answers = this.state.given_answers.filter((e) => !del_answers.includes(e));

		given_answers.push(answer);

		let res = { given_answers };

		if (this.calcScore(given_answers) < 0) {
			res.step = this.state.count;
			this.setState(() => { return res; });
			return;
		}

		let step = 0;

		let next_step = this.state.step + 1;
		if (next_step >= this.state.count) {
			step = this.state.count;
		}

		while(!step && next_step < this.state.count) {
			let question = this.state.json.questions[next_step-1];

			let deps = question.depends;
			if (!deps) {
				step = next_step;
			} else {
			    let met_deps = 0;
			    deps.forEach((dep) => {
				    given_answers.forEach((answer) => {
					    if (dep === answer) {
						    ++met_deps;
					    }
				    });
			    });

			    if (0 < met_deps) {
			    	if (question.operator && question.operator === 'or') {
						step = next_step;
			    	}
			    	if (met_deps === deps.length) {
						step = next_step;
			    	}
				}
			}
			next_step++;
		}

		if (!step || step >= this.state.count) {
			step = this.state.count;
		}

		res.step = step;

		this.setState(() => { return res; });

	};

	findScores = (scores, answers, first_answer) => {
		for (let i = 0; i < answers.length; ++i) {
			let a = answers[i];
			scores[ a.answer_id ] = Array.isArray(a.score) ? a.score[ first_answer ] : a.score;
		}
		return scores;
	};

	calcScore = (prelim_answers) => {
		if (!this.state.json) {
			return 0;
		}

		let given_answers = prelim_answers || this.state.given_answers;
		let questions = this.state.json.questions;
		let scores = [];
		let first_answer = given_answers[0] || 0;
		for (let i = 0; i < questions.length; ++i) {
			let q = questions[i];
			this.findScores(scores, q.answers, first_answer);
			if (q.sub) {
                q.sub.forEach((sub) => {
				    this.findScores(scores, sub.answers, first_answer);
                });
			}
		}
		let score = given_answers.reduce((sum, i) => sum + scores[i], 0);
		return score;
	};

	getResult = () => {
		var score = this.calcScore();
		var results = this.state.json.results;
		for (var i = 0; i < results.length; ++i) {
			var res = results[i];
			if (   (res.up_to && score < res.up_to)
				|| (res.in    && res.in.includes(score))
				|| (!res.in   && !res.up_to))
			{
				return res.html;
			}
		}
		return '';
	};

	render() {
		let status_step = 0;
		if (this.state.step)
			status_step = (this.state.step >= this.state.count) ?
				this.state.steps :
				this.state.json.questions[this.state.step-1].step;

		return (
			<div className="test-wrapper">
				<TransitionGroup>
				{0 === this.state.step && !this.state.json &&
					<Loading key="loading" />
				}
				{0 === this.state.step && this.state.json &&
					<TestIntro
						key="testintro"
						intro={this.state.json.intro}
						start={this.startTest}
					/>
				}
				{0 < this.state.step && this.state.step >= this.state.count &&
					<Result
						key="result"
						html={this.getResult()}
					/>
				}
				{0 < this.state.step && this.state.step < this.state.count &&
					<Question
						key={"question" + this.state.step}
						question={this.state.json.questions[this.state.step - 1]}
						given_answers={this.state.given_answers}
						recordAnswerAndStep={this.recordAnswerAndStep}
/*
						recordAnswer={this.recordAnswer}
						stepTest={this.stepTest}
*/
					/>
				}
				{0 < this.state.step &&
					<Status
						key="status"
						step={status_step}
						count={this.state.steps}
					/>
				}
				</TransitionGroup>
			</div>
		);
	}
}

DanmarkTesten.propTypes = {
	quiz: PropTypes.string,
	json: PropTypes.object
};

export default DanmarkTesten;



